import React from "react"
import LayautPages from "./layautPages"
import {connect} from "react-redux"
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';

import ProductComments from "./productComments"
import Carousel from "./carousel"
import TecnicTable from "./tecnicTable"

import { useMediaQuery } from 'react-responsive'


function Product(props){

	const smallComputer = useMediaQuery( { query: ' ( max-device-width: 800px ) ' } )


	const useStyles = makeStyles((theme) => ({

		 container: {
		 	borderTopLeftRadius: 10,
		 	borderTopRightRadius: 10,
		    backgroundColor: theme.palette.background.paper,
		    padding:  theme.spacing(4, 3, 6),
		    marginLeft: smallComputer ? 0 : "20%",
		    marginRight: smallComputer ? 0 : "20%",
		    marginTop: smallComputer ? 0 : "15px"
		  	
		 		  },

		  abstractContainer: {
		
		  }


		}));


	const classes = useStyles();
	var product = props.products.find(prod => prod.id == props.match.params.productId);



	const ProductData = () => {

	if (product) {
	//	console.log(product)
		return(
		
		
			<div className = {classes.container} >

			<div className={classes.abstractContainer}>  
				  <Typography
	              variant="h5"
	              align="left"
	              color="textPrimary"
	              gutterBottom
	            >
	            	{product.name}

	            </Typography>

	             <Typography
	              variant="body1"
	              align="justify"
	              color="textPrimary"
	              paragraph
	            >

	            	{product.abstract}

	            </Typography>
	        </div>    

	     
	     	<Carousel
	     		imageURL = {product.images}

	     	/>

	     	<TecnicTable
	     		tableData = {product.tableData}
	     	/>


	            {product.especifications.map((e, index) => (
	            

		            <div key = {index} >
				            	 <Typography
				              variant="h6"
				              align="left"
				              color="textPrimary"
				              gutterBottom
				            	>
				            	{e[0]}
				            </Typography>
		            

		            
				            	 <Typography
				              variant="body1"
				              align="justify"
				              color="textSecondary"
				              paragraph
				            >
				            	{e[1]}
				            </Typography>
		            </div>
		     

	            	))}

	         
	            <ProductComments
	            	productId = {product.id}
	            	productName = {product.name}
	            /> 



	            </div> 


	            

	       

            

		
			);

	} else {

		return(  <h2> El producto no existe </h2>  ); 

	}


	}


	return(
		<LayautPages>
			<ProductData/>
		</LayautPages>

		);
}


const mapStateToProps = (state) => {
	return{
		products: state.products
	}
}


export default connect(mapStateToProps)(Product)
