import React from 'react';
import { Route, Switch, BrowserRouter, MemoryRouter, HashRouter  } from 'react-router-dom';
import InitPage from "./components/initPage"
import CategoriasPage from "./components/categoriasPage"
import AboutPage from "./components/aboutPage"
import Product from "./components/product"


 //<BrowserRouter basename={process.env.PUBLIC_URL}>
function MyRouter() {

    return (
    	<HashRouter>
	    	<Switch>
		     	<Route exact path="/" component = {InitPage}/>
		        <Route path="/nosotros" component = {AboutPage} />		  
		        <Route path="/productos/:productId" component = {Product} />
	        </Switch>
	    </HashRouter>
   
    );
  }


export default MyRouter;

		    
